import React, {useState, useEffect} from 'react';
import './loan_details.css'
import Modal from "react-modal";
import LoanOptions from "../loan_options/loan_options";
import SelectedLoan from "../tables/selected_loan";
import {useMutation} from "react-query";
import {Api} from '../../tools/api'
import {Form, Button} from "react-bootstrap";
import {Alert, Box, TextField} from "@mui/material";
import moment from 'moment';


const minDate = new Date(moment().subtract(19, "year"));
const maxDate = new Date(moment().subtract(69, "year"));

const minDatedd = minDate.getDate();
const minDatemm = minDate.getMonth() + 1; //January is 0 so need to add 1 to make it 1!
const minDateyyyy = minDate.getFullYear();
const maxDatedd = maxDate.getDate();
const maxDatemm = maxDate.getMonth() + 1; //January is 0 so need to add 1 to make it 1!
const maxDateyyyy = maxDate.getFullYear();

const minDateVal = minDateyyyy + '-' + minDatemm + '-' + minDatedd;
const maxDateVal = maxDateyyyy + '-' + maxDatemm + '-' + maxDatedd;

export default function LoanDetails({
                                        visibility, info, membershipProp, showOptions,
                                        data, calculatorProps, user, setVisibility,
                                    }) {
    // get change info from loan context -- used with inputs
    const [employmentDetails, setEmploymentDetails] = useState(false)
    const [agreement, setAggrement] = useState(false)
    const [index, setIndex] = useState();
    const [isOpen, setIsOpen] = useState(false);
    const [userDetails, setUserDetails] = useState({
        fname: '',
        sname: '',
        phoneNumber: '',
        email: '',
        omang: '',
        dob: '',
    })

    const [api] = useState(new Api());

    // saving client to db
    const {
        mutate: sendSMS,
        isSuccess: sendSMSSuccess,
    } = useMutation('sendSMS', api.sendSMS, {retry: 2});

    const {
        mutate: addApp,
        isSuccess: addAppSuccess,
    } = useMutation('addLoanApplication', api.addLoanApplication, {retry: 2});

    const {
        mutate: saveClient,
    } = useMutation('calculateLoan', api.addClient, {retry: 2});

    useEffect(() => {
        if (sendSMSSuccess && addAppSuccess) {
            // setIsOpen(true);
        }
    }, [sendSMSSuccess, addAppSuccess])


    useEffect(() => {
        if (membershipProp === "yes") {
            setUserDetails({
                fname: user.fname,
                sname: user.sname,
                phoneNumber: user.phoneNumber,
                email: user.email,
                omang: user.omang,
                dob: user.dob,
            })
        }
        // if (userDetails) {
        //     getClientDetails()
        // }
    }, [membershipProp, user.fname, user.sname, user.phoneNumber, user.email, user.omang, user.dob],)

    const handleExisting = async () => {
        const app = {
            ...user,
            ...calculatorProps,
            allLoanOptions: data,
            chosenLoanOption: data[index],
            appliedOn: new Date(),
            status: 'pending',
            resolvedOn: '',
            resolvedBy: '',
            isSealed: index
        }
        const params = {
            recipient: app.phoneNumber,
            message: "Your application has been received. Our representatives will contact you as soon as possible.",
        }

        await sendSMS(params)
        await addApp(app)
        setIsOpen(!isOpen);
        window.location.reload(false);
    }

    const handleSubmit = async (e) => {
        e.preventDefault()

        let isExisting = true

        if (visibility.newCustomer) {
            isExisting = false
        }

        const params = {
            recipient: userDetails.phoneNumber,
            message: "Your application has been received. Our representatives will contact you as soon as possible.",
        }
        // save client to db
        const client = {

            ...userDetails,
            ...calculatorProps,
            isExisting: isExisting,
        }
        // save loan application
        const app = {
            allLoanOptions: data,
            chosenLoanOption: data[index],
            ...client,
            appliedOn: new Date(),
            status: 'pending',
            resolvedOn: '',
            resolvedBy: '',
            isExisting: isExisting,
            isSealed: index
        }

        await saveClient(client)
        await addApp(app)
        await sendSMS(params)
        setIsOpen(!isOpen)
        window.location.reload(false);

    }

    return (
        <section className="site-root pt-28 lg:pt-0 lg:mt-0 mx-16 mb-600px lg:mb-auto">
            {(showOptions && data) ?
                <LoanOptions className="loan-options mt-96 lg:mt-0 pl-20 " visibility={visibility}
                             showOptions={showOptions} info={info}
                             data={data}
                             setIndex={setIndex}/> : null

            }
            {index > -1 ?
                <SelectedLoan
                    info={info}
                    visibility={visibility}
                    showOptions={showOptions}
                    data={data}
                    agreement={agreement}
                    setAggrement={setAggrement}
                    employmentDetails={employmentDetails}
                    setEmploymentDetails={setEmploymentDetails}
                    handleExisting={handleExisting}
                    user={user}
                    index={index}
                    setIndex={setIndex}
                    setVisibility={setVisibility}

                /> :
                (!showOptions) &&
                <div className="invisible lg:visible">
                    <span className="banner_text">A loan to suit your needs </span>
                    <div id="arrowAnim">
                        <div className="arrowSliding">
                            <div className="arrow"/>
                        </div>
                        <div className="arrowSliding delay1">
                            <div className="arrow"/>
                        </div>
                        <div className="arrowSliding delay2">
                            <div className="arrow"/>
                        </div>
                        <div className="arrowSliding delay3">
                            <div className="arrow"/>
                        </div>
                    </div>
                </div>
            }

            {visibility.newCustomer && employmentDetails &&
            <div className="personal-details mt-5 pl-8 lg:pl-6 pt-5 mb-4 lg:ml-7">
                <Form onSubmit={e => handleSubmit(e)}>
                    <h3>Personal Details</h3>
                    <Box className="flex-row lg:mx-16"
                         sx={{
                             '& .MuiTextField-root': {m: 2},
                         }}
                    >
                        <div className="flex-col w-96">
                            <TextField
                                className=""
                                type="text"
                                label="First Names"
                                variant="outlined"
                                required={true}
                                value={userDetails.fname}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={(e) => setUserDetails(
                                    {...userDetails, fname: e.target.value})}/>
                            <TextField
                                className="border-2 mb-1"
                                type="text"
                                label="Surname"
                                variant="outlined"
                                required={true}
                                value={userDetails.sname}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={(e) => setUserDetails(
                                    {...userDetails, sname: e.target.value})}/>

                            <TextField
                                className="border-2 mb-1"
                                type="text"
                                label="Phone Number"
                                variant="outlined"
                                required={true}
                                value={userDetails.phoneNumber}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={(e) => setUserDetails(
                                    {...userDetails, phoneNumber: e.target.value})}/>
                        </div>
                        <div className="flex-col w-96">
                            <TextField
                                className="border-2 mb-1"
                                type="email"
                                label="Email Address"
                                variant="outlined"
                                required={true}
                                value={userDetails.email}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={(e) => setUserDetails(
                                    {...userDetails, email: e.target.value})}/>
                            <TextField
                                className="border-2 mb-1 mr-96"
                                type="text"
                                label="ID Number/ Passport"
                                variant="outlined"
                                required={true}
                                value={userDetails.omang}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={(e) => setUserDetails(
                                    {...userDetails, omang: e.target.value})}/>
                            <TextField
                                className="border-2 mb-1 mr-96"
                                type="date"
                                label="Date of birth"
                                variant="outlined"
                                required={true}
                                value={userDetails.dob}
                                defaultValue={new Date(moment().date())}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                InputProps={{
                                    inputProps: {min: {maxDateVal}, max: {minDateVal}}
                                }}
                                onChange={(e) => setUserDetails(
                                    {...userDetails, dob: e.target.value})}/>
                        </div>
                    </Box>

                    <br/>
                    {(maxDateVal <= userDetails.dob && userDetails.dob <= minDateVal) ?
                        <div className="button-proceed button-space -mt-6 lg:mt-0"
                             style={{
                                 display: 'flex',
                                 justifyContent: 'center'
                             }}>
                            <Button style={{
                                margin: "10px auto",
                                width: '30%',
                                height: '30%',
                                padding: '10px',
                                backgroundColor: '#010f3e',
                                color: 'white',
                                borderRadius: '5px',
                                border: 'solid 1px #010f3e',
                                fontSize: 'large',
                                textAlign: 'center',
                            }} type="submit">
                                Submit
                            </Button>
                        </div> :
                        <div className="lg:mx-14 mx-4">
                            {userDetails.dob ?
                                <Alert severity="warning">
                                    You have to be aged between 19 and 69 to continue  please correct your date of
                                    birth!
                                </Alert> :
                                <Alert severity="info">
                                    Please complete the form to continue
                                </Alert>
                            }
                        </div>
                    }
                </Form>
            </div>
            }
            <Modal
                isOpen={isOpen}
                onRequestClose={() => setIsOpen(!isOpen)}
                contentLabel="My dialog"
                className="modal"
                overlayClassName="myoverlay"
            >

                <div className="membership-modal">
                    <div className="close" onClick={(e) => setIsOpen(!isOpen)}>close</div>
                    <div style={{textAlign: 'center'}} className="title">
                        <h1>
                            Thank you for your interest in a personal loan. A call center agent will contact you soon.
                        </h1>
                    </div>
                    <br/>
                    <div className="button-proceed button-space"
                         style={{
                             display: 'flex',
                             justifyContent: 'center'
                         }}>
                        <Button style={{
                            margin: "10px auto",
                            width: '20%',
                            height: '30%',
                            padding: '10px',
                            backgroundColor: '#010f3e',
                            color: 'white',
                            borderRadius: '5px',
                            border: 'solid 1px #010f3e',
                            fontSize: 'large',
                            textAlign: 'center',
                        }} onClick={() => setIsOpen(!isOpen)}>
                            Done
                        </Button>
                    </div>
                </div>
            </Modal>
        </section>
    );
}