import axios from 'axios';
//TODO config file update
/*
* Please edit the line below to update the location of the config.js file
*
* */
import node_server_url from "./config.js"

/**
 * Class containing functions for interacting with the api.
 */
export class Api {

    // url of node server
    #nodeUrl = node_server_url;
    // base urls
    #clientURL = `${this.#nodeUrl}/calcapi/clients`;
    #loanAppURL = `${this.#nodeUrl}/calcapi/loan-applications`;
    #employerURL = `${this.#nodeUrl}/calcapi/employers`;

    // for calculating a loan
    #calculateLoanUrl = `${this.#nodeUrl}/calculateInternal`;
    // for sending an SMS
    #sendSMSUrl = `${this.#nodeUrl}/sendSMS`;


    // NODE SERVER FUNCTIONS

    /**
     * Function used to calculate the loan details
     * @param formData - info from the fields used to calculate the loan
     * @returns Array of different loan options
     */
    calculateLoan = async (formData) => {
        // get response from node server endpoint
        const response = await axios.post(this.#calculateLoanUrl, {"data": formData});
        // return the data if successful
        return response.data.loanOptions;
    }

    /**
     * Function used to send an SMS to a phone number
     * @param params object of the form {recipient: '26771234567', message: 'Hello World'}
     * @return message Index or error with description
     */
    sendSMS = async (params) => {
        // get response from node server endpoint
        const response = await axios.post(this.#sendSMSUrl, {"data": params});
        // return data is successful
        return response.data;
    }

    /**
     * Get array of clients from db
     * @param filters Array of objects of form {field: 'name', value: 'John'}
     * @return array of clients or [] if query has no match
     */
    getClient = async (filters) => {

        // stores response from api
        let res;
        // check if filter is omang -- undefined if not found
        let omangFilter = filters.find(filter => filter.field === 'omang');
        // get omang from filter\
        const clientOmang = omangFilter.value;
        // use omang to get data
        res = await axios.get(`${this.#clientURL}/${clientOmang}`);

        const data = res.data

        if (data[0].isExisting) {
            return data
        } else {
            return []
        }
    }

    /**
     * Add a new client to database
     * @param client Client object with all fields
     */
    addClient = async (client) => {
        // get response from node server endpoint
        return await axios.post(`${this.#clientURL}/new`, client);
    }

    /**
     * Add an application for a loan. Used as leads in dashboard
     * @param application Object of structure {client: 'client object', chosenLoanOption: 'loan option object',
     * allLoanOptions: 'array of all loan option objects'}
     */
    addLoanApplication = async (application) => {
        // structure according to what API expects
        const {chosenLoanOption} = application;
        // console.log('application',application)
        const body = {
            omang: application.omang,
            selected: application.index,
            affordability: chosenLoanOption.affordabity,
            rates: chosenLoanOption.rate,
            loan_term: chosenLoanOption.LoanTerm,
            maximum_amount: chosenLoanOption.maxLoan,
            take_home: chosenLoanOption.TakeHome,
            installment: chosenLoanOption.Installment,
            basicSalary: application.BasicSalary,
            grossSalary: application.GrossSalary,
            netPay: application.NetPay,
            totalFixedAllowances: application.TotalFixedAllowances,
        }

        // get response from node server endpoint
        return await axios.post(`${this.#loanAppURL}/new`, body);
    }

    /**
     * @param filters Array of filters to use
     */
    getLoanApplications = async (filters) => {
        let res;
        // check if filter is omang -- undefined if not found
        let omangFilter = filters.find(filter => filter.field === 'omang');
        if (omangFilter.length > 0) {
            // get omang from filter
            const clientOmang = omangFilter[0].value;
            // use omang to get data
            res = await axios.get(`${this.#loanAppURL}`, {params: {omang: clientOmang}});
        } else {
            // in all other cases, get all clients
            res = await axios.get(`${this.#loanAppURL}`);
        }
        return res.data;

    }
    /**
     * @return array of employers or [] if db is empty
     */
    getEmployers = async () => {
        let res;
        res = await axios.get(`${this.#employerURL}/all`);
        return res.data;
    }


}
