import React from 'react';
import './loan_options.css'
import LoanDetailsTable from "../tables/loan_details_table";
import {Alert, AlertTitle} from "@mui/material";

function LoanOptions(props) {
    return (
        <section className="
        ml-8
        lg:ml-28
        ">
            {!(props.data.length > 0) ? <>
                    <Alert severity="error" className="my-11">
                        <AlertTitle>Error</AlertTitle>
                        This is an error alert  <strong>Please check payslip details and try again</strong>
                        <li>Current Affordability (Per payslip) = Net - ((Gross - (Basic + Fixed allowances))</li>
                        <li>When the input values do not satisfy the above the calculator doesnt return any offers</li>
                    </Alert> </> :
                <div className="loan-options mt-96 lg:mt-0">
                    <h1>Loan Options Available For You</h1>
                    <span>Please select the offer that suits you:</span>
                    {<LoanDetailsTable data={props.data} setIndex={props.setIndex}/>}
                </div>}
        </section>

    );
}

export default LoanOptions;